@use "../global/variables.scss";

.page {
  height: 100%;
}

.page__body {
  height: 100%;
  font-family: variables.$font-RobotoSlab;
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5rem;
  color: variables.$color-primary;
  margin: 0;
  text-wrap: balance;
  min-height: 100%;
}

.page__container {
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: variables.$color-bg;
}
