body {
  margin: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  display: block;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "RobotoSlab";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/RobotoSlab-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "RobotoSlab";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/RobotoSlab-Bold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "VinaSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/VinaSans-Regular.woff2") format("woff2");
  font-display: swap;
}
.description {
  margin: 0;
  padding: 0;
}

.link {
  display: block;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.title {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  padding: 0 9.375vw;
  margin: 0 auto;
  text-align: start;
  max-width: 1150px;
}

.page {
  height: 100%;
}

.page__body {
  height: 100%;
  font-family: "RobotoSlab", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5rem;
  color: #000000;
  margin: 0;
  text-wrap: balance;
  min-height: 100%;
}

.page__container {
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}

.main-nav {
  padding: 1rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (hover: hover) and (pointer: fine) {
  .main-nav *:hover {
    transition: opacity 0.15s;
    opacity: 0.7;
  }
  .main-nav *:active {
    transition: opacity 0.15s;
    opacity: 0.4;
  }
}

.main-header {
  border-bottom: 2px solid #000000;
  margin-bottom: 1.5rem;
}

.quotes__wrapper {
  padding-bottom: 1rem;
}

.quotes__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.quotes__textarea {
  text-wrap: pretty;
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
}

.quotes__description {
  margin: 0;
  padding: 0;
  padding-bottom: 1.5rem;
  position: relative;
  display: flex;
  gap: 5vw;
}
.quotes__description::before {
  content: "";
  display: block;
  flex-shrink: 0;
  width: min(3vw, 1rem);
  background-color: #e2e2e2;
}

.quotes__source {
  text-align: end;
  font-size: 1rem;
  display: block;
}

.hero__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 1.5rem;
}

.hero__title {
  font-family: "VinaSans", "Tahoma", sans-serif;
  font-size: clamp(2rem, 7vw, 4rem);
  line-height: clamp(2rem, 7vw, 4rem);
  padding: 1.5rem 0;
}

.hero__button {
  padding: 1rem;
  border: 2px solid #000000;
  border-radius: 8px;
  background-color: transparent;
  text-transform: uppercase;
  font-family: "RobotoSlab", "Tahoma", sans-serif;
  font-weight: 700;
  letter-spacing: 0.2px;
  font-size: clamp(0.2rem, 5vw, 1rem);
  width: 33vw;
}
@media (hover: hover) and (pointer: fine) {
  .hero__button:hover {
    transition: opacity 0.15s;
    opacity: 0.7;
  }
  .hero__button:active {
    transition: 0.15s;
    transform: translate(0, 2px);
  }
}

.main-container {
  flex-grow: 1;
}

.main-footer__wrapper {
  padding-top: 1rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: flex-end;
  border-top: 2px solid #000000;
}

.copyright-link {
  width: fit-content;
  display: flex;
  align-items: center;
  gap: 1rem;
}
@media (hover: hover) and (pointer: fine) {
  .copyright-link:hover {
    transition: opacity 0.15s;
    opacity: 0.7;
  }
  .copyright-link:active {
    transition: opacity 0.15s;
    opacity: 0.4;
  }
}